import React from "react";
import ImageGallery from "react-image-gallery";
import "../../../node_modules/react-image-gallery/styles/css/image-gallery.css";
import "./style.css";
class Photos extends React.Component {
  render() {
    return (
      <div id="photos">
        <div id="gallery">
          <div id="title-main"><b>Nasza propozycja:</b></div>
          <div className="house item">
            <div className="title item">
              <b>Biały mieszkalny domek 15 m.</b>
            </div>
            <div className="people item"><b>2 osobowy</b></div>
            <div className="costs item"><b>Od 200zł/doba</b></div>
            <div className="main">
              <ImageGallery
                items={[
                  {
                    original:
                      "https://a.allegroimg.com/s512/03fda1/9077094046358837e37d94bab4a2/Przyczepa-Kempingowa-Hobby",
                    thumbnail:
                      "https://a.allegroimg.com/s512/03fda1/9077094046358837e37d94bab4a2/Przyczepa-Kempingowa-Hobby"
                  },
                  {
                    original:
                      "http://www.rainbowtruck.pl/_/rsrc/1445189988665/nasz-bus/RAinbowtruck%20budowa%20busa%205.JPG?height=240&width=320",
                    thumbnail:
                      "http://www.rainbowtruck.pl/_/rsrc/1445189988665/nasz-bus/RAinbowtruck%20budowa%20busa%205.JPG?height=240&width=320"
                  }
                ]}
              />
              <div className="description">
                Jakiś tam opis Chomiczak szary prowadzi nocny tryb życia.
                Czasami kopie własne nory, choć bardziej lubi mieszkać w tych
                wykopywanych przez inne gryzonie. W razie niebezpieczeństwa
                ukrywa się w niej, gdyż tak jak myszy i szczury ma krótkie
                łapki, które uniemożliwiają szybki bieg. Pożywienie – nasiona
                trzyma w odległości 100-500 m za norą. Chomiczak nie zapada w
                sen zimowy ani odrętwienie (jest mało aktywny), dlatego, żeby
                zapewnić sobie pokarm, przed zimą zbiera do 800 g nasion.
              </div>
            </div>
          </div>

          <div className="house item">
            <div className="title item">
              <b>Czarny domek mieszkalny 20m.</b>
            </div>
            <div className="people item">3 osobowy</div>
            <div className="costs item">Od 250zł/doba</div>
            <div className="main">
              <ImageGallery
                items={[
                  
                  {
                    original:
                      "https://miro.medium.com/max/4096/1*ICV8fX9YQAa0ySGZca9zUQ.jpeg",
                    thumbnail:
                      "https://miro.medium.com/max/4096/1*ICV8fX9YQAa0ySGZca9zUQ.jpeg"
                  },
                  {
                    original:
                      "http://cdn22.urzadzamy.smcloud.net/t/photos/t/21566/kamper_2542406.jpg",
                    thumbnail:
                      "http://cdn22.urzadzamy.smcloud.net/t/photos/t/21566/kamper_2542406.jpg"
                  }
                ]}
              />
              <div className="description">
                Jakiś tam opis Chomiczak szary prowadzi nocny tryb życia.
                Czasami kopie własne nory, choć bardziej lubi mieszkać w tych
                wykopywanych przez inne gryzonie. W razie niebezpieczeństwa
                ukrywa się w niej, gdyż tak jak myszy i szczury ma krótkie
                łapki, które uniemożliwiają szybki bieg. Pożywienie – nasiona
                trzyma w odległości 100-500 m za norą. Chomiczak nie zapada w
                sen zimowy ani odrętwienie (jest mało aktywny), dlatego, żeby
                zapewnić sobie pokarm, przed zimą zbiera do 800 g nasion.
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Photos;
