import React from "react";
import "./style.css";
import InfoIcon from "@material-ui/icons/Info";
import PhotoIcon from "@material-ui/icons/Photo";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import ContactMailIcon from "@material-ui/icons/ContactMail";
import LocalOfferIcon from "@material-ui/icons/LocalOffer";

class Menu extends React.Component {
  render() {
    return (
      <div id="navbar">
        <div className="link">Domki Rewa</div>
        <div>
          <a href="#about">
            <InfoIcon className="icon" fontSize="large"/>
          </a>
          <a href="#geo">
            <LocationOnIcon className="icon" fontSize="large"/>
          </a>

          <a href="#photos">
            <LocalOfferIcon className="icon" fontSize="large"/>
          </a>

          <a href="#contact">
            <ContactMailIcon className="icon" fontSize="large"/>
          </a>
        </div>
      </div>
    );
  }
}

export default Menu;
